@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.searching_form{
    margin-left: 65px;
    margin-right: 95px;
    padding-top: 40px
  }
@font-face {
  font-family: broadway;
  src: url(/Pokedex/static/media/Broadway.2d8844b6.ttf); 
 }

.footer{
  background-color: #3f51b5;
  color: white;

  font-family: "broadway";
  text-align: center;

  display:flex;
  flex-direction: column;
  justify-content: center;

  height: 70px;
}
.menu{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }

.head_menu{
    height: 65px;
    width: 250px;
    text-align: center;

    font-size: 20px;
    font-weight: bold;
  
    color: white;
    background-color: black;
}

.head_text{
    text-decoration: none;
    color: rgb(255,255,255);
}

.head_text:active{
    font-size: 90%;
}
  
.menu_section{
    height: 30px;
    margin-left: 30px;
    padding-top: 15px;
}
  
.menu_section a{
    font-size: 16px;
    text-decoration: none;

    color: #3C3838;
}
  
.menu_section p:hover, .menu_section a:hover{
    color: black;
}
.card{
    height: 140px;
    width: 140px;

    margin-top: 30px;
    margin-right: 30px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    background-image: url(/Pokedex/static/media/card_background.36022bff.jpg);
}

.img_of_card{
    height: 110px;

    display:flex;
    justify-content: center;
}

.img_of_card img{
    width: 110px;
}

.text_of_card{ 
    text-align: center;
    text-decoration: none;
    background-color: rgba(0,0,0,0.4);
}

.text_of_card .head_card{
    text-decoration: none;
    font-style: italic;
    font-weight: bold;
    font-size: 14px;
    
    color: white;
}

.text_of_card .head_card:hover{
    font-size: 101%;
}

.card_info{
    width: 250px;
    height: 310px; 
}

.card_info .head_of_card{
    padding: 5px 0;
    
    font-size: 20px; 
    font-weight: bold;
    
    display: flex;
    justify-content: space-around;

    background-color: #aa137a;
    color: white;
}

.card_info .skils{
    padding: 5px;
}

.card_info .skils div{
    display: flex;
    justify-content: space-between;
}

.img_card{
    display: flex;
    justify-content: center;
}

.img_card img{
    width: 140px;
}

.skils .name{
    width: 200px;
    margin-bottom: 2px;

    font-style: italic;
    font-weight: bold;

    background-color: #aa137a;
}

.skils .indicator{ 
    width: 30px;
    margin-bottom: 2px;

    font-weight: bold;

    background-color: #0beaf9;
}
.background_card{
    background-color: #f2e9e9;
    display: flex;
    justify-content: center;
    
    align-items: center;
    min-height: 90vh;
}

.info_card{
    background-color: rgb(255,255,255);
    width: 50vw;
    margin-top: 50px;
    padding-bottom: 25px;
}

.info_card .head{
    height: 65px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 5px;

    font-size: 24px;
    font-weight: bold;
    font-style: italic;

    background-color: #d90707;
    color: rgb(255,255,255);
}

.info_card .types{
    display: flex;
    justify-content: center;
}

.info_card .types .type{
    margin: 10px;

    font-weight: bold;
    font-style: italic;

    border-radius: 8px;
    padding: 5px 10px;

    color: rgb(255,255,255);
}

.info_card .types .one{
    background-color: #aa137a;
}

.info_card .types .two{
    background-color: #0beaf9
}

.info_card .main_info{
    display: flex;
    justify-content: space-around;

    margin: 0px;
}

.info_card .main_info .image{   
    display: flex;
    align-items: center; 
}

.info_card .main_info .image img{
    width: 12vw;
}

.info_card .main_info .skils{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 20vw;

    font-size: 16px;
}

.info_card .main_info .skils div{
    display: flex;
    justify-content: space-between;
}

.info_card .main_info .count{
    background-color: #d4d4d4;
    width: 75%;
    
    text-align: right;
    margin-bottom: 3px;
}


.info_card .profile{
    margin: 0 1%; 
}

.info_card h4{
    height: 25px;    

    font-style: italic;
    text-align: center;

    background-color: #d90707;
    color: rgb(255,255,255);
}

.info_card .profile .info_profile{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    font-size: 16px;
}

.info_card .profile .info_profile div{
    margin-bottom: 3px;

    width: 45%;

    display: flex;
    justify-content: space-between;
}

.info_card .profile .info_profile .prop{
    font-weight:  bold;
    width: 40%;
}

.info_card .profile .info_profile .c_profile{
    width: 50%;
}

@media screen and (max-width: 380px)
{
   .info_card .main_info{
       flex-direction: column;
       justify-content: center;
       align-items: center;
   }

   .info_card .main_info .image img{
       min-width: 50vw;
   }

   .info_card .main_info .skils{
       min-width: 90%;
    }

}

@media screen and (max-width: 420px)
{
    .info_card .profile .info_profile{
        justify-content: center;
        align-items: center;
        flex-direction: column;
    
        font-size: 14px;
    }

    .info_card .profile .info_profile div{
        width: 90%;
    }
}

@media screen and (max-width: 480px)
{
    .info_card{
        width: 90vw;
        margin-top: 75px;
        margin-bottom: 20px;
    }

    .info_card .main_info .image img{
        width: 20vw;
    }

    .info_card .main_info .skils{
        width: 60vw;
    }

    .info_card .main_info .count{
        width: 70%;
    }

    .info_card .profile .info_profile div{
        font-size: 14px;
        min-width: 47%;
    }
    
    .info_card .profile .info_profile .prop{
        min-width: 60%;
    }
    
    .info_card .profile .info_profile .c_profile{
        width: 35%;
    }
}

@media screen and (max-width: 1024px)
{
    .info_card{
        width: 80vw;
    }

    .info_card .main_info .image img{
        width: 20vw; 
    }

    .info_card .main_info .skils{
        width: 40vw;
    }
}

@font-face {
  font-family: Goudos;
  src: url(/Pokedex/static/media/Goudos.78d7bdc5.TTF); 
 }
 
 @font-face{
  font-family: Snap;
  src: url(/Pokedex/static/media/Snap.8b64e15c.ttf);
 }
 
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app{
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  min-height: 100vh;
}

.body{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.info{
  min-height: 70vh;
  margin-top: 90px;
}

.info h1, .info p{
  margin: 30px;
  width: 85%;

  font-family: 'Arial', sans-serif;
}

.result_of_search{
  padding: 15px;

  font-family: "Goudos";
  font-size: 18px;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.result_of_search p{
  margin: 1px;
}

.result_of_search .ref{
   font-family: "Snap";
   font-size: 18px;

   color: blue;
}

.result_of_search .ref:hover{
  color:red;
  cursor: pointer
}

.start{
  margin-top: 65px;
  min-height: 85vh;
}


.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

